import React from 'react';
import {
  SearchBar, Tabs, Steps,
} from 'antd-mobile';

export default class Demo extends React.Component {
  componentDidMount() {
    this.props.changeTitle('Stage 2');
  }
  render() {
    return (<div style={{ marginBottom: 30 }}>

      <SearchBar placeholder="" />

      <Tabs tabs={[{ title: '' }, { title: '' }]} renderTab={tab => tab.title}>
        <Steps direction="horizontal">
          <Steps.Step title="" description="" />
          <Steps.Step title="113" description="" />
          <Steps.Step title="114" description="" />
        </Steps>
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: 2000 }}>
           tab content 
        </div>
      </Tabs>
    </div>);
  }
}



