import React from "react";
import { Card, WingBlank, WhiteSpace, List } from "antd-mobile";

//less
import "./StudentsMessage.less";

const Item = List.Item;
const Brief = Item.Brief;
export default class Demo extends React.Component {
  state = {
    disabled: false
  };
  componentDidMount() {
    this.props.changeTitle("");
  }
  render() {
    return (
      <div>
        <WingBlank size="sm">
          <WhiteSpace size="sm" />
          <Card>
            <Card.Header
              thumb="http://img1.touxiang.cn/uploads/20120816/16-004551_548.jpg"
              thumbStyle={{ width: "60%" }}
              extra={
                <div>
                  <span></span>
                  <h6></h6>
                </div>
              }
            />
            <Card.Body>
              <Item>8</Item>
              <Item>130cm</Item>
              <Item>30kg</Item>
            </Card.Body>
            <Card.Footer />
          </Card>
          <WhiteSpace size="sm" />
        </WingBlank>
        <List
          renderHeader={() => ""}
          extra={<span>...</span>}
          className="my-list"
        >
          <Item arrow="horizontal" multipleLine onClick={() => {}}>
             <Brief>12</Brief>
          </Item>
          <Item
            arrow="horizontal"
            multipleLine
            onClick={() => {}}
            platform="cross"
          >
            
            <Brief></Brief>
          </Item>
          <Item arrow="horizontal" multipleLine onClick={() => {}}>
             <Brief></Brief>
          </Item>
        </List>
      </div>
    );
  }
}



